@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }
input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-logo:before { content: ""; }

.icon-mouse:before { content: ""; }

.icon-play:before { content: ""; }

.icon-user:before { content: ""; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -10px; font: 0/0 a; text-shadow: none; color: transparent; height: 20px; background: none; left: 0; z-index: 1; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 30px; line-height: 30px; content: "prev"; display: inline-block; vertical-align: middle; font-size: 15px; color: #fff; background: #000; }

.slick-next { right: 0; left: auto; }
.slick-next:before { content: "next"; display: inline-block; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 0; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { background: #000; }
.slick-dots button { display: block; width: 10px; height: 10px; background: #fff; box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25); cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }

@font-face { font-family: Lato-Black; src: url("fonts/Lato-Black.eot"); src: url("fonts/Lato-Black.eot?#iefix") format("embedded-opentype"), url("fonts/Lato-Black.woff2") format("woff2"), url("fonts/Lato-Black.woff") format("woff"), url("fonts/Lato-Black.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: Lato; src: url("fonts/Lato-Regular.eot"); src: url("fonts/Lato-Regular.eot?#iefix") format("embedded-opentype"), url("fonts/Lato-Regular.woff2") format("woff2"), url("fonts/Lato-Regular.woff") format("woff"), url("fonts/Lato-Regular.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: RobotoSlab-Bold; src: url("fonts/RobotoSlab-Bold.eot"); src: url("fonts/RobotoSlab-Bold.eot?#iefix") format("embedded-opentype"), url("fonts/RobotoSlab-Bold.woff2") format("woff2"), url("fonts/RobotoSlab-Bold.woff") format("woff"), url("fonts/RobotoSlab-Bold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.in, .header__in { margin: 0 auto; padding: 0 5px; max-width: 1080px; *zoom: 1; }
.in:after, .header__in:after { content: " "; display: table; clear: both; }

body { font-family: "Lato", Arial, sans-serif; font-size: 19px; line-height: 1.4; }

a { color: inherit; text-decoration: none; }

.header .menu { float: right; }
.header__left { float: left; }

.menu.is-active .menu__btn:before { transform: translateY(11px) rotate(45deg); }
.menu.is-active .menu__btn:after { transform: translateY(-12px) rotate(-45deg); }
.menu.is-active .menu__btn span:before { transform: translateY(240px); }
.menu.is-active .menu__list { visibility: visible; opacity: 1; z-index: 1; }
.menu.is-active .menu__list li { transform: translate(0, 0); opacity: 1; }
.menu__btn { position: relative; padding-top: 35px; width: 35px; font-family: "Lato-Black", Arial, sans-serif; line-height: 1; background: transparent; }
.menu__btn:before, .menu__btn:after, .menu__btn span:before { position: absolute; content: ""; left: 0; right: 0; height: 5px; background: #000; transition: transform 0.2s; }
.menu__btn:before { top: 0; }
.menu__btn:after { top: 23px; }
.menu__btn span:before { top: 11px; transition: transform 0.6s ease-in; }
.menu__list { font-family: "Lato", Arial, sans-serif; font-size: 24px; }
.menu__list li { will-change: opacity; transform: translate3d(0, 0, 0); transition: transform 0.4s ease-out, opacity 0.4s; transform: translate(40%, -30%); opacity: 0; }
.menu__list li:nth-child(1) { transition-delay: 0s; }
.menu__list li:nth-child(2) { transition-delay: 0.1s; }
.menu__list li:nth-child(3) { transition-delay: 0.2s; }
.menu__list li:nth-child(4) { transition-delay: 0.3s; }
.menu__list li:nth-child(5) { transition-delay: 0.4s; }
.menu__list li:nth-child(6) { transition-delay: 0.5s; }

.top { background: #ffe94a; }


